import $ from 'jquery';
import clone from 'clone';
import scrollTo from '../../js-utils/scroll-to';
import scrolledIntoView from '../../js-utils/scrolled-into-view';

/**
 * Recursively assigns unique IDs to each tree node based on hierarchy.
 * @param {Object} tree - The current node in the tree.
 * @param {Object} map - A mapping of node IDs to nodes.
 * @param {string|null} parentId - The ID of the parent node.
 */

function prepareTreeIds(tree, map, parentId = null) {
    tree.parentId = parentId;
    tree.id = (parentId ? parentId + '.' : '') + tree.index;
    map[tree.id] = tree;
    tree.children.forEach(child => prepareTreeIds(child, map, tree.id));
}

function searchParams() {
    const params = ['dev', 'SQ_DESIGN_NAME', 'SQ_PAINT_LAYOUT_NAME']
        .map(key => ({
            key,
            match: location.search.match(new RegExp(`[?&]${key}=([^&]*)`)),
        }))
        .filter(p => p.match)
        .map(({ key, match }) => `${key}=${match[1]}`);

    return (params.length > 0 ? '?' : '') + params.join('&');
}

/**
 * Initializes the vertical menu helper.
 * @param {jQuery} menuContainer - The container element for the menu.
 * @param {Object} tree - The menu structure.
 * @param {Array} defaultOpen - List of default open menu items.
 * @param {boolean} scroll - Whether to enable scrolling to the active item.
 * @param {number} [maxSiblings=0] - Maximum number of sibling items to display.
 */
export default class VerticalMenuHelper {
    constructor(menuContainer, tree, defaultOpen, scroll, maxSiblings = 0) {
        this.scroll = scroll;
        this.menuContainer = menuContainer;
        this.tree = clone(tree);
        this.rootLevel = tree.level;
        this.maxSiblings = maxSiblings > 1 && maxSiblings;
        this.maxChildrenLevelLimit = this.maxSiblings
            ? this.rootLevel + defaultOpen.length - 1
            : 0;

        this.searchQueryStr = searchParams();

        this.defaultOpen = [this.tree];
        defaultOpen
            .slice(1)
            .forEach(({ index }, i) =>
                this.defaultOpen.push(this.defaultOpen[i].children[index])
            );

        this.levelMap = {};
        prepareTreeIds(this.tree, this.levelMap);

        this.prepareItemElement(this.tree, 'div');

        const toggleChildren = this.toggleChildren.bind(this);
        this.menuContainer.on('click', '[data-toggle]', function(evt) {
            toggleChildren($(this).attr('data-toggle'));
            evt.preventDefault();
        });
    }

    toggleChildren(item, forceOpen = false) {
        if (typeof item === 'string') item = this.levelMap[item];

        if (item.children.length > 0) {
            let ancestors = item.element.parents('.vertical-menu');
            let setOpen = forceOpen || !item.element.hasClass('open');

            if (setOpen) {
                this.menuContainer
                    .find('.vertical-menu.open')
                    .removeClass('open');
            }

            item.element.toggleClass('open', setOpen);

            if (setOpen) {
                ancestors.addClass('open');
                const childrenToDisplay =
                    item.level < this.maxChildrenLevelLimit &&
                    item.children.length > this.maxSiblings
                        ? item.children.filter(({ type }) => type > 0)
                        : item.children;

                childrenToDisplay.forEach(item =>
                    this.prepareItemElement(item)
                );
            }

            if (this.scroll && scrolledIntoView(item.element) < 1) {
                scrollTo(item.element);
            }
        }

        this.menuContainer
            .find(
                '.vertical-menu.open > .vertical-menu__header > .vertical-menu__toggle-link'
            )
            .attr('aria-label', 'Hide submenu')
            .attr('aria-expanded', 'true');

        this.menuContainer
            .find(
                '.vertical-menu:not(.open) > .vertical-menu__header > .vertical-menu__toggle-link'
            )
            .attr('aria-label', 'Show submenu')
            .attr('aria-expanded', 'false');
    }

    launch() {
        this.defaultOpen.forEach(item => {
            this.prepareItemElement(item);
            this.toggleChildren(item, true);
        });

        if (this.scroll) {
            this.defaultOpen[
                this.defaultOpen.length - 1
            ].element[0].scrollIntoView();
        }
    }

    closeAll() {
        this.menuContainer.find('.open').removeClass('open');
    }

    /**
     * Prepares and appends the HTML structure for a menu item.
     * @param {Object} item - The menu item.
     * @param {String} wrapper - Name of tag to use as wrapper for element.  Default li, to wrap the element insied <li></li>
     */
    prepareItemElement(item, tag = 'li') {
        if (!item.element) {
            let element = $(`<${tag}></${tag}>`);

            if (item.name) {
                element.addClass(`vertical-menu vertical-menu--l${item.level}`);
                if (item.class) {
                    element.addClass(`${item.class}`);
                }
                let header = $('<div></div>')
                    .addClass('vertical-menu__header')
                    .appendTo(element);

                let pageLink;
                if (item.url && item.url !== '#') {
                    // Regular clickable link
                    pageLink = $('<a class="vertical-menu__page-link"></a>')
                        .addClass(`vertical-menu__page-link--t${item.type}`)
                        .attr('href', item.url + this.searchQueryStr);
                } else if (item.url === '#' && item.children.length > 0) {
                    // If URL is #, and has children make it a toggle button
                    pageLink = $(
                        '<a role="button" href="#" class="vertical-menu__page-link"></a>'
                    ).attr('data-toggle', item.id);
                } else {
                    pageLink = $(
                        '<span class="vertical-menu__page-link"></span>'
                    ).addClass(`vertical-menu__page-link--t${item.type}`);
                }

                // Add Font Awesome icon before text
                if (item.icon) {
                    let iconElement = $(
                        `<span class="fa ${item.icon} vertical-menu__icon" aria-hidden="true"></span>`
                    );
                    pageLink.append(iconElement);
                }

                pageLink
                    .append(
                        $(
                            '<span class="vertical-menu__page-link__label"></span>'
                        ).html(item.name)
                    )
                    .appendTo(header);

                if (item.level > this.rootLevel && item.children.length > 0) {
                    let childrenToggle = $(
                        `<a role="button" href="#" class="vertical-menu__toggle-link">
                        <span class="fa" aria-hidden="true"></span></a>`
                    );
                    childrenToggle
                        .attr('data-toggle', item.id)
                        .appendTo(header);
                }
            }

            if (item.parentId === null) {
                element.prependTo(this.menuContainer);
            } else {
                element.appendTo(
                    this.levelMap[item.parentId].childrenContainer
                );
            }

            item.element = element;

            if (item.children.length > 0) {
                item.childrenContainer = $('<ul></ul>').appendTo(item.element);
            }
        }
    }
}
