import $ from 'jquery';
import {
    contentGroupCn,
    headingAnchorCn,
    headingAnchorIconCn,
    headingAnchorLabelCn,
    headingCn,
} from './element-classnames';

function prepareAccordionHeading(patternClassname, text, index, id) {
    let anchor = $('<a/>')
        .addClass(`${headingAnchorCn(patternClassname)} inactive`)
        .attr('href', '#' + id)
        .attr('data-id', index)
        .attr('data-accordion-anchor', true);

    $('<span/>')
        .html(text)
        .addClass(headingAnchorLabelCn(patternClassname))
        .appendTo(anchor);

    $('<span/>')
        .attr('aria-hidden', 'true')
        .addClass(headingAnchorIconCn(patternClassname))
        .appendTo(anchor);

    return anchor;
}

export default function findHeadings(className, widget, contentWrapper) {
    let firstHeading = contentWrapper.find('h2,h3,h4');
    let headingTagName = firstHeading.prop('tagName');

    let headerElements = contentWrapper.find(headingTagName);

    return headerElements.toArray().map((element, i) => {
        let header = $(element)
            .attr('data-id', i)
            .addClass(headingCn(className));

        let id = element.id;

        let parents = header.parentsUntil(contentWrapper);
        let wrapper = parents.length > 0 ? parents.last() : null;

        if (!wrapper) {
            let contentElements = [];
            let next = header.next();
            while (next.length > 0 && next.prop('tagName') !== headingTagName) {
                contentElements.push(next);
                next = next.next();
            }

            wrapper = $('<div></div>')
                .insertBefore(header)
                .append(header)
                .append(contentElements);
        }

        wrapper.addClass(contentGroupCn(className));

        let anchor = prepareAccordionHeading(className, header.html(), i, id);

        anchor.insertBefore(wrapper);

        return {
            id: i,
            text: header.html(),
            anchor,
        };
    });
}
