import $ from 'jquery';
import Cookies from 'js-cookie';

const COOKIE_NAME = 'cookienoticeshown';

function launch(el) {
    const widget = $(el);
    if (!Cookies.get(COOKIE_NAME)) {
        Cookies.set(COOKIE_NAME, '1', { expires: 365 });

        widget.show();

        widget.find('.cookie-notice__dismiss button').click(function(event) {
            event.preventDefault();
            widget.fadeOut('fast');
        });
    }
}

const className = 'cookie-notice';

export default {
    launch,
    className,
};
