import $ from 'jquery';
import Cookies from 'js-cookie';

const COOKIE_NAME = 'alertclosed';

function launch(el) {
    const widget = $(el);
    if (!Cookies.get(COOKIE_NAME) && $('.quick-alert')) {
        $('.quick-alerts').addClass('open');

        widget.find('.quick-alert__close a').click(function(event) {
            event.preventDefault();
            var inOneHour = new Date(new Date().getTime() + 60 * 60 * 1000);
            Cookies.set(COOKIE_NAME, '1', { expires: inOneHour });
            $('.quick-alerts').removeClass('open');
        });
    }
}

const className = 'quick-alerts';

export default {
    launch,
    className,
};
