import {
    current as globalCurrent,
    tree as globalTree,
} from '../nav-mobile/parse-nav';

function processTree() {
    if (globalCurrent.length < 2) {
        return { tree: null, current: null };
    }

    const tree = globalCurrent
        .slice(1, 2)
        .reduce(
            (acc, currentItem) =>
                acc.children.find(child => child.url === currentItem.url),
            globalTree
        );
    const current = globalCurrent.slice(1);

    return { current, tree };
}

export const { tree, current } = processTree();
