import $ from 'jquery';

function launch(el) {
    const widget = $(el);

    widget.find('.noticeboard__notice').each(function() {
        var notice = this;

        $(notice)
            .find('.noticeboard__notice__close')
            .click(function() {
                event.preventDefault();
                $(notice).removeClass('active');
                widget.removeClass('active');
            });

        $(notice)
            .find('.noticeboard__notice__title')
            .click(function() {
                event.preventDefault();
                $(notice).addClass('active');
                widget.addClass('active');
            });
    });
}

const className = 'noticeboard__list';

export default {
    launch,
    className,
};
