import $ from 'jquery';

function launch(el) {
    const widget = $(el);

    var infowindow = new google.maps.InfoWindow(),
        marker,
        lat,
        lng,
        prev_infowindow = false,
        currentMarkerId = widget.attr('data-locationId'),
        placeName = widget.attr('data-placename');

    //get centre coordinates
    function getCoordinates(selector) {
        lat = $(selector).attr('data-lat');
        lng = $(selector).attr('data-lng');
        lat = parseFloat(lat);
        lng = parseFloat(lng);
        var coordinates = [lat, lng];
        return coordinates;
    }

    var initialCoordinates = getCoordinates(widget),
        centrePoint = new google.maps.LatLng(
            initialCoordinates[0],
            initialCoordinates[1]
        );

    var map = new google.maps.Map(el, {
        zoom: 17,
        center: centrePoint,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
    });

    function dropMarker(myLatLng, markerMsg, markerId) {
        marker = new google.maps.Marker({
            position: myLatLng,
            map: map,
            animation: google.maps.Animation.DROP,
        });
        attachMessage(marker, markerMsg, markerId);
    }

    function attachMessage(marker, markerMsg, markerId) {
        var infowindow = new google.maps.InfoWindow({
            content: markerMsg,
        });

        if (currentMarkerId === markerId) {
            prev_infowindow = infowindow;
            infowindow.open(map, marker);
        }

        marker.addListener('click', function() {
            if (prev_infowindow) {
                prev_infowindow.close();
            }
            prev_infowindow = infowindow;
            infowindow.open(map, marker);
        });
    }

    var myLatLng = new google.maps.LatLng(lat, lng);
    dropMarker(myLatLng, placeName, currentMarkerId);
}

const className = 'induction__event__location__map';

export default {
    launch,
    className,
};
