import $ from 'jquery';

function launch(el) {
    const widget = $(el),
        todayKey = new Date().toISOString().substr(0, 10),
        todaySelector = '[data-day="' + todayKey + '"]',
        maybeToday = $(todaySelector);

    console.info('launch accordion--induction');

    if (maybeToday.length > 0) {
        var toggleEl = maybeToday.parent().prev('.inactive');
    } else {
        var toggleEl = widget.find('.inactive').first();
    }

    toggleEl
        .filter('.inactive')
        .removeClass('inactive')
        .addClass('active')
        .attr('aria-selected', 'true')
        .attr('aria-expanded', 'true');

    if (
        $('.accordion__content__heading')
            .first()
            .attr('data-day') !== todayKey &&
        maybeToday.length > 0
    ) {
        $('html, body').animate(
            {
                scrollTop: toggleEl.offset().top,
            },
            'slow'
        );
    }
}

const className = 'accordion--induction';

export default {
    launch,
    className,
};
