import $ from 'jquery';
import { openModalDialog } from '../dialog/dialog';

/*
<a href="#" data-link="www.EXAMPLE.com">link</a>
<div class="popup-dialog">
    <div class="popup-dialog__header">
        HEADER
    </div>
    <div class="popup-dialog__body">
        CONTENT
        <a href="www.EXAMPLE.com" class="popup-dialog__nojs-link">link</a>
    </div>
</div>
*/

const className = 'popup-dialog';

function launch(el) {
    let anchors = $('a[data-link]');
    let link = anchors.attr('data-link');
    let title = $('.popup-dialog__header').html();

    let content = $('<div></div>').append($('.popup-dialog__body').html());

    let acceptButton = $('<a></a>')
        .attr('href', link)
        .text('Accept')
        .addClass('cta cta-inline');

    let cancelButton = $('<a></a>')
        .attr('href', '#')
        .text('Cancel')
        .addClass('cta cta-inline');

    $('<div></div>')
        .addClass('cta-group cta-group--stretched')
        .append(acceptButton)
        .append(cancelButton)
        .appendTo(content);

    anchors.click(function(e) {
        e.preventDefault();
        openModalDialog(title, content, cancelButton);
    });
}

export default {
    launch,
    className,
};
